* {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
}

*:hover,
*:focus {
    border: none;
    outline: none;
}

.App {
    text-align: center;
}

.App-header {
    background-color: #282c34;
    height: 60px;
    display: flex;
    padding: 0 24px;
    align-items: center;
    justify-content: space-between;
    color: white;
}

.App-link {
    color: #61dafb;
}

main {
    background-color: #dedddd;
    padding: 60px 24px;
    height: 100%;
    min-height: calc(100vh - 60px);
}

input, button {
    color: white;
    padding: 16px 24px;
    outline: none;
    font-size: 20px;
    border: none;
    transition: all .3s;
    margin: 10px 0;
}

input::placeholder {
    color: #dbdbdb;
    font-size: 16px;
}

input {
    background: #3e08ac;
}

button {
    text-align: center;
    background: #9200a8;
}

label > span {
    margin-right: 16px;
    display: inline-block;
}

button:hover {
    opacity: 0.7;
}
